$primary: #34353a;
$secondary: #2E2F34;
$tertiary: #FFFFFF;
$textprimary: #E6AE4D;

.skill-background {
  background-color: $primary;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left:10%;
  padding-right:10%;
}

.battery {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 10px 10px;
    width: 92%;
    border: solid thin $tertiary;
}

.bar {
    width: 3%;
    border: solid thin rgba(255, 255, 255, 0.1);
    padding: 2%;
    height: 80%;
    background: $secondary;
}

.active-border {
    width: 3%;
    border: solid thin rgba(255, 255, 255, 0.1);
    padding: 2%;
    height: 80%;
    background: $tertiary;
}

.bar-heading
{
    font-family: 'Roboto', sans-serif;
    color: $textprimary;
    font-weight:400;
    font-size: 18px;
    position: absolute;
    top: -45px;
    @media (max-width:800px) {
        font-size: 14px;
        top: -35px;
    }
}
 
