.battery-container{
    position: relative;
    width: 750px;
    margin-top: 50px;
    @media (max-width:1600px) {
        width: 600px;
    }
    @media (max-width:1300px) {
        width: 500px;
    }
    @media (max-width:1040px) {
        width: 90%;
    }
}

.batteries{
    justify-content: space-around;
    align-content: center;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 50px;
}